body {
  background: linear-gradient(45deg, rgb(25, 86, 128), blue);
  min-height: 100vh;
  margin: 0;
  padding: 0;
  display: grid;
  place-items: center;
}

.todo-list {
  margin: auto;
  width: fit-content;
  background-color: darkblue;
  box-shadow: 2px 2px 5px black;
  padding: 20px;
  display: flex;
  flex-direction: column;
}

.todo-item {
  color: white;
  font-size: 2rem;
  /* background-color: white; */
  margin: 10px;
}
.todo-item > p {
  margin-left: 15px;
  display: inline;
}

.todo-item > input {
  width: 2rem;
  height: 2rem;
}
@media (max-width: 400px) {
  .todo-item {
    padding: 30px 20px;
  }
}
